  cursor: pointer;
  -webkit-transition-property: opacity;
  -moz-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;

  .i0 {
    position: absolute;
    width: 32px;
    height: 32px;
    top: 0;
    left: 0;
    background: url("data:image/gif;base64,R0lGODlhAQABAIABAP///wAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw==");
    border: none;
  }

  i {
    position: absolute;
    width: 4px;
    height: 4px;
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    -o-box-sizing: content-box;
    -ms-box-sizing: content-box;
    box-sizing: content-box;
    border: 1px solid #111;
  }

  .i1 .i1, .i2 .i2, .i3 .i3, .i4 .i4 {
    width: 3px;
    height: 3px;
    border-color: #eee;
    top: 0;
    left: 0;
    margin: 0 !important;
  }

  .i1 {
    top: 7px;
    left: 7px;
    border-right: none;
    border-bottom: none;
    margin: 2px 0 0 2px;
  }

  .i2 {
    top: 7px;
    left: 18px;
    border-left: none;
    border-bottom: none;
    margin: 2px 0 0;
  }

  .i3 {
    top: 18px;
    left: 18px;
    border-top: none;
    border-left: none;
  }

  .i4 {
    top: 18px;
    left: 7px;
    border-right: none;
    border-top: none;
    margin: 0 0 0 2px;
  }

  &.fotorama__fsi_hover {
    .i1, .i2, .i3, .i4 {
      margin: 1px 0 0 1px;
    }
  }
}

.fotorama_fullscreen .fotorama__fsi {
  .i1, .i2, .i3, .i4 {
